<template>
  <form class="login-form pure-form flex-item-top ml-auto mr-auto pure-form-stacked inline-block" @submit.prevent="submit">
    <label for="email" class="text-light">Email</label>
    <input type="email" id="email" name="email" required>
    <label for="password" class="text-light">Password</label>
    <input type="password" id="password" name="password" required>
    <div class="flex-column">
      <button class="ml-auto pure-button pure-button-primary" type="submit">Login</button>
      <small class="ml-auto text-light">
        don't have account?
        <router-link class="link" to="/Register">Register</router-link>
      </small>
    </div>
  </form>
</template>

<script>

export default {
  data() {
    return {
      flag: false,
    };
  },
  methods: {
    submit(event) {
      if (this.flag) {
        return;
      }
      this.flag = true;
      const form = event.target;
      const formData = new FormData(form);
      this.$axios.post(this.$url.login, formData)
        .then((resp) => {
          const { token, user } = resp.data;
          localStorage.setItem('jwt', token);
          localStorage.setItem('user', JSON.stringify(user));
          this.$axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
          this.flag = false;
          this.$router.push('/');
          this.$bus.$emit('login');
        })
        .catch((err) => {
          this.flag = false;
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.login-form {
  width: 250px;
  position: relative;
  margin: 5% auto;
  input {
    width: 100%;
  }
}
</style>
